@import './reset.scss';
.user{
    .userTop{
        z-index:999;
        width: 100%;
        height: rem(48px);
        background: #89be48;
        position: fixed;
        top: 0;
        color:#fff;
        display: flex;
        justify-content: space-between;
        align-items: center;
        box-sizing: border-box;
        padding:0 rem(10px);
        .countCenter{
            font-size: rem(20px);
        }
        .userOptions{
            color:#fff;
            font-size: rem(14px);
        }
    }
    .userStatus{
        width: 100%;
        height: rem(108px);
        margin-top: rem(48px);
        background-image: url(http://i1.lifevccdn.com/images/h5/core/background.png);
        background-size: 100% 100%;
        color:#333;
        font-size: rem(16px);
        padding:rem(15px) 0;
        box-sizing: border-box;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: center;
        .userBox{
            width: rem(133px);
            height: rem(38px);
            background: hsla(0,0%,100%,.4);
            border: 1px solid #009c42;
            border-radius: rem(20px);
            a{
                color:#009c42;
            }
            display: flex;
            justify-content: space-between;
            align-items: center;
            padding:0 rem(20px);
            box-sizing: border-box;
            .userLogin{
                border-right:1px solid #009c42;
                padding-right: rem(13px);
            }
        }
    }
    .userOperate{
        width: 100%;
        overflow: hidden;
        background-color: #fff;
        border-top: 1px solid #eee;
        margin: 0 auto;
        ul{
            width: 100%;
            height: rem(61px);
            overflow: hidden;
            border-bottom: 1px solid #eee;
            a{
                width: 33%;
                height: rem(30px);
                margin: rem(18px) 0 0;
                box-sizing: border-box;
                padding:0 rem(10px);
                color: #585858;
                float: left;
                border-right: 1px solid #dedede;
                display: flex;
                align-items: center;
                .optWord{
                    display: inline-block;
                    text-align: center;
                    width: rem(42px);
                    font-size: rem(14px);
                }
                .optBg{
                    display: inline-block;
                    height: rem(42px);
                    width: rem(46px);
                    background-repeat: no-repeat;
                }
                .optBg1{
                    background-image: url(data:image/png;base64,);
                    background-size: 90%;
                }
                .optBg2{
                    background: url(data:image/png;base64,);
                    background-size: 90%;
                }
                .optBg3{
                    background: url(data:image/png;base64,);
                    background-size: 90%;
                    background-position:rem(3px) 0;
                }
                .optBg4{
                    background: url(data:image/png;base64,);
                    background-size: 90%;
                }
                .optBg5{
                    background: url(data:image/png;base64,);
                    background-size: 90%;
                }
                .optBg6{
                    background: url(data:image/png;base64,);
                    background-size: 90%;
                }
            }
            li:nth-child(3){
                border-right:none;
            }
        }
    }
    .userBottom{
        background-color: #eee;
        padding-bottom: rem(60px);
        .btmBox{
            margin-bottom: rem(12px);
            .userList{
                width: 100%;
                height: 62px;
                color:#585858;
                font-size: rem(14px);
                background-color: #fff;
                border-bottom: 1px solid #eee;
                box-sizing: border-box;
                display: flex;
                justify-content: space-between;
                align-items: center;
                padding:0 rem(15px);
                .itemTitle{
                    padding-right: rem(150px);
                }
                i{
                    font-size: rem(22px);
                }
                img{
                    width: rem(30px);
                    height: rem(30px);
                }
            }
        }
    }
    .userDidLog{
        .userBox{
            width: 100%;
            height: rem(108px);
            margin-top: rem(48px);
            background-image: url(http://i1.lifevccdn.com/images/h5/core/background.png);
            background-size: 100% 100%;
            color:#333;
            font-size: rem(16px);
            padding:0 rem(80px);
            box-sizing: border-box;
            display: flex;
            justify-content: space-between;
            align-items: center;
            position: relative;
            img{
                width: rem(55px);
                height: rem(55px);
            }
            .imgupload{
                position: absolute;
                top: rem(25px);
                left: rem(80px);
                position: absolute;
                width: rem(55px);
                height: rem(55px);
                border-radius: 50%;
                opacity: 0;
            }
            .userMsg{
                box-sizing: border-box;
                padding-right: rem(50px);
                .userLevel{
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    img{
                        width: rem(21px);
                        height: rem(21px);
                    }
                    i{
                        font-size: rem(24px);
                        color:#ccc;
                    }
                }
            }
        }
    }
}