@import './reset.scss';
.header{
    position: fixed;
    top: 0;
    z-index:999;
    .top{
        width: rem(375px);
        height: rem(48px);
        background: #89be48;
        .headInfo{
            width: 100%;
            height: 100%;
            background: url(data:image/png;base64,) no-repeat;
            background-size: 50%;
            background-position: 50%;
            .toSearch{
                width: rem(44px);
                height: rem(44px);
                position: absolute;
                z-index: 1000;
                top: 0;
                right: 0;
                background-size: 45%;
                background-image: url(data:image/png;base64,);
                background-repeat: no-repeat;
                background-position: 50%;
            }
        }
    }
    .nav{
        width:rem(375px);
        height: rem(48px);
        color:#333;
        background-color: #fff;
    }
    .swiper-slide{
        line-height: rem(45px);
        font-size: rem(14px);
        text-align: center;
        border-bottom: 2px solid #eee;
        box-sizing: border-box;
    }
    .sel{
        color: #80b532;
        border-bottom: 2px solid #80b532;
    }
}